.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.furkan{
  color: red;
}
.navbar{
  height: 50px;
  line-height: 50px;
  width: 100%;
  background-color: #ddd;
  color:black;
  margin: 0 auto;
  letter-spacing: 2px;
  font-weight:bold;

}
.navbar a{
  color:black;
  text-decoration:none;
  margin-right: 20px;
  font-weight: bold;
}
.a{
  height: 25px;
  background-color: red;
  width: 100%;
}
.govde{
  height: 50vh;
  width: 100%;
  background-color: #61dafb;
  color:black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container{
  height: 50%;
  width: 50%;
  margin:0 auto;

}
